import $ from 'jquery';

declare const Pickr: any;

$( createPicker );

function createPicker() {
	const colorPickers = $( '.twrp-color-picker' );

	colorPickers.each( function() {
		const element: any = this;
		const input = $( element ).parent().find( 'input' );
		let inputVal = input.val();

		if ( ! inputVal ) {
			inputVal = null;
		}

		const pickr = Pickr.create( {
			el: element,
			theme: 'classic',
			container: 'body',
			default: inputVal,
			appClass: 'twrp-pickr',

			swatches: [
				'rgba(244, 67, 54, 1)',
				'rgba(233, 30, 99, 1)',
				'rgba(156, 39, 176, 1)',
				'rgba(103, 58, 183, 1)',
				'rgba(63, 81, 181, 1)',
				'rgba(33, 150, 243, 1)',
				'rgba(3, 169, 244, 1)',
				'rgba(0, 188, 212, 1)',
				'rgba(0, 150, 136, 1)',
				'rgba(76, 175, 80, 1)',
				'rgba(139, 195, 74, 1)',
				'rgba(205, 220, 57, 1)',
				'rgba(255, 235, 59, 1)',
				'rgba(255, 193, 7, 1)',
			],
			defaultRepresentation: 'RGBA',

			components: {
				// Main components
				preview: true,
				opacity: true,
				hue: true,

				// Input / output Options
				interaction: {
					input: true,
					cancel: true,
					clear: true,
					save: true,
				},
			},
		} ).on( 'save', function( color: any ) {
			if ( color ) {
				input.val( color.toRGBA().toString( 0 ) );
			} else {
				input.val( '' );
			}

			input.change();
			pickr.hide();
		} );

		pickr.setColorRepresentation( 'RGBA' );
	} );
}
